*{
  padding: 0;
  margin: 0;
  color: white;
}
.hole-frame{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  background-color: #4158D0;
  background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
}
.background{
  width: 100%;
  background-color: white;
  margin-top: 100px;
  width: 400px;
  height: 500px;
  border-radius: 9px;
  background: linear-gradient(135deg, rgba(245, 181, 255, 0.1), rgba(198, 2, 247, 0));
  border: 1px solid white;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 0 20px black;
}
.mainfarme{
  margin-left: 50vb;
  margin-top: 20vh;
  padding: 10px;
  width: 400px;
  height: 500px;
  background-color: black;
  border-radius: 20px;
  display: flex;
  align-content: center;
  border: 1px solid black;
}
button{
  width: 90px;
  height: 50px;
  margin: 3px;
  background-color: dimgray;
  border: 2px solid white;
  border-radius: 25px;
  cursor: pointer;
  border: 3px solid black;
  transition: 1s;
}
.gray button{
  background-color: paleturquoise;
  color: black;
  transition: 1s;
}
input{
  width: 390px;
  height: 100px;
  border: 1px solid white;
  margin: 3px;
  border-radius: 5px;
  font-size: 40px;
  background: linear-gradient(135deg, rgba(245, 181, 255, 0.1), rgba(198, 2, 247, 0));
  border: 1px solid white;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}
#orange{
  background-color: orange;
  transition: 1s;
}
#n0{
  width: 190px;

}
button:hover{
  background-color: aqua;
  box-shadow: 0 0 10px aqua;
}
#orange:hover{
  box-shadow: 0 0 10px orange;
}
.gray button:hover{
  box-shadow: 0 0 10px paleturquoise;
  background-color: paleturquoise;
}
